import { yupResolver } from "@hookform/resolvers/yup";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";
import { NexusInput, NexusSelect } from "components";
import { ButtonType, RoomType } from "enums";
import { useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import * as yup from "yup";
import { Action, ActionType } from "../../reducer";
import { createNewRoom } from "../../utils";

interface Props {
  open: boolean;
  dispatch: React.Dispatch<Action<ActionType>>;
}

const timeframe = {
  [RoomType.ICEBREAKER]: 15,
  [RoomType.COFFEE_TIME]: 30,
  [RoomType.MEET_AND_CHILL]: 60,
};

export const CreateRoomForm = ({ open, dispatch }: Props) => {
  const { t } = useTranslation();
  const [isLoading, setIsLoading] = useState(false);

  const roomTypes = Object.values(RoomType).map(value => ({
    value,
    label: `${t(`rooms.${value}`)} - ${timeframe[value]} ${t("common.minutes")}`,
  }));

  const requiredMessage = t("common.is_required");

  const validationSchema = yup.object().shape({
    name: yup.string().required(`${t("common.name")} ${requiredMessage}`),
    type: yup.string().required(`${t("common.room_type")} ${requiredMessage}`),
  });

  const methods = useForm({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      name: "",
      type: RoomType.COFFEE_TIME,
    },
  });

  const onSubmit = async (values: unknown) => {
    const { name, type } = values as { name: string; type: string };
    try {
      setIsLoading(true);
      await createNewRoom(t, dispatch, name, type);
    } catch (e) {
      console.error(e);
    }
    setIsLoading(false);
  };

  let bottomSection = <CircularProgress />;
  if (!isLoading) {
    bottomSection = (
      <>
        <Button onClick={() => dispatch({ type: ActionType.CLEAR_DIALOG })}>
          {t("common.no")}
        </Button>

        <Button type={ButtonType.SUBMIT} autoFocus>
          {t("common.yes")}
        </Button>
      </>
    );
  }

  return (
    <Dialog open={open}>
      <FormProvider {...methods}>
        <form
          onSubmit={methods.handleSubmit(onSubmit)}
          id="create-room-form"
          data-testid="create-room-form"
        >
          <DialogTitle>{t("rooms.create_a_new_room")}</DialogTitle>
          <DialogContent>
            <NexusInput label={`${t("common.name")} *`} name="name" variant="standard" />
            <NexusSelect
              label={`${t("common.room_type")} *`}
              name="type"
              variant="standard"
              items={roomTypes}
            />
          </DialogContent>
          <DialogActions>{bottomSection}</DialogActions>
        </form>
      </FormProvider>
    </Dialog>
  );
};
